import React from 'react';
import styled from 'styled-components';
import bgImage from '../static/header.png';
import * as logo from '../static/Logo.png';
import vkLogo from '../static/vk.png';
import instLogo from '../static/instagram.png';
import velcomLogo from '../static/velcom.png';
import mtsLogo from '../static/mts.png';


const HeaderWrapper = styled.div`
  display: flex;
  justify-content: center;
`;

const HeaderImage = styled.img`
  margin: auto;
  width: 100%;
`;

const HeaderText = styled.div`
  display: flex;
  position: absolute;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  padding: 5px;
  justify-content: space-between;
  background-color: rgba(255,255,255, 0.4);
  box-sizing: border-box;
`;

const Block = styled.div`
  display: flex;
  align-items: center;
`;

const Logo = styled.img`
  width: 60px;
  height: 60px;
  box-shadow: 0 0 3px black;
`

const CompanyName = styled.div`
  margin-left: 5px;
  font-size: 1.2em;
`;

const Phone = styled.div`
  display: flex;
  align-items: center;
  margin-left: 10px;
`;

const PhoneLogo = styled.img`
  width: 25px;
  height: 25px;
  margin-right: 5px;
`;

const MediaLogo = styled.img`
  width: 30px;
  height: 30px;
  border-radius: 3px;
  margin: 0 10px;
  cursor: pointer;
`;

const Header = () => (
  <HeaderWrapper id="main">
    <HeaderImage src={bgImage} />
    <HeaderText>
      <Block>
        <Logo src={logo} />
        <CompanyName>ГомельСтроитель</CompanyName>
      </Block>
      <Block>
        <Phone><PhoneLogo src={velcomLogo} />+375 29 952-99-40</Phone>
        <Phone><PhoneLogo src={mtsLogo} />+375 44 444-44-44</Phone>
      </Block>
      <Block>
        <a
          href="https://vk.com/id427319348"
          target="_blank"
          rel="noopener noreferrer"
        >
          <MediaLogo src={vkLogo} />
        </a>
        <a
          href="https://www.instagram.com/gomelstroitel"
          target="_blank"
          rel="noopener noreferrer"
        >
          <MediaLogo src={instLogo} />
        </a>
      </Block>
    </HeaderText>
  </HeaderWrapper>
);

export default Header;
