import React from 'react';
import styled from 'styled-components';

const JobWrapper = styled.li`
  display: flex;
  align-items: center;
  margin: 10px 0;
`;

const JobImage = styled.img`
  width: 100px;
  height: 100px;
  margin-right: 7px;
  border: 1px solid black;
  border-radius: 50%;
`;

const JobDescription = styled.div`

`;

const JobTitle = styled.h3`

`;

const JobText = styled.p`

`;

const Job = ({job}) => (
  <JobWrapper>
    <JobImage src={job.img} />
    <JobDescription>
      <JobTitle>{job.title}</JobTitle>
      <JobText>{job.text}</JobText>
    </JobDescription>
  </JobWrapper>
);

export default Job;
