import React from 'react';
import styled from 'styled-components';


const GreetingWrapper = styled.div`
  text-align: center;
`;

const Greeting = () => (
  <GreetingWrapper>
    <h2>Здравствуйте, уважаемые Заказчики! Рады Вас привествовать на нашем сайте!</h2>
    <p>Мы занимаемся строительными работами. Выполняем как отдельные виды работ, так и комплексы работ «под ключ».</p>
  </GreetingWrapper>
);

export default Greeting;
