import React from 'react';
import styled from 'styled-components';

import Job from './Job';
import { jobs } from '../data/jobs/jobs';

const Title = styled.h2`
  text-align: center;
`;


const Row = styled.div`
  display: flex;
`;

const Column = styled.div`
  flex: 50%
`;

const List = styled.ul`
  list-style: none;
`;

const JobsList = () => {
  const jobs1 = jobs.slice(0, 8);
  const jobs2 = jobs.slice(8);
  return (
    <>
      <Title id="jobs">Виды работ</Title>
      <Row>
        <Column>
          <List>
            { jobs1.map((job, i) => <Job key={i} job={job}/>) }
          </List>
        </Column>
        <Column>
          <List>
            { jobs2.map((job, i) => <Job key={i} job={job}/>) }
          </List>
        </Column>
      </Row>
    </>
  );
};

export default JobsList;

