import React from 'react';
import styled from 'styled-components';

const Title = styled.h2`
  text-align: center;
`;

const ListWrapper = styled.div`
`;

const List = styled.ol`

`;

const Stage = styled.li`

`;

const StagesList = () => (
  <ListWrapper>
    <Title id="stages">Наша работа включает следующие этапы</Title>
    <List>
      <Stage>Общение по телефону</Stage>
      <Stage>Выезд специалиста</Stage>
      <Stage>Расчёт стоимости работ</Stage>
      <Stage>Составление сметно-договорной документации для банка (в случае кредитного строительства)</Stage>
      <Stage>Геодезические работы</Stage>
      <Stage>Доставка материалов</Stage>
      <Stage>Выполнение работ</Stage>
    </List>
  </ListWrapper>
);

export default StagesList;
