import React from 'react';
import styled from 'styled-components';

import vkLogo from '../static/vk.png';
import instLogo from '../static/instagram.png';

const GalleryWrapper = styled.div`
  text-align: center;
`

const Title = styled.h2`
  text-align: center;
`;

const MediaBlock = styled.div`
  display: flex;
  justify-content: center;
`;

const MediaLink = styled.a`
  display: block;
`;

const MediaLogo = styled.img`
  width: 80px;
  height: 80px;
  border-radius: 3px;
  margin: 0 30px;
  cursor: pointer;
`;


const Gallery = () => (
  <GalleryWrapper id="gallery">
    <Title>Наши работы</Title>
    <p>Галерею наших выполненных работ Вы можете увидеть в Вконтакте или в Инстаграмме по ссылкам ниже:</p>
    <MediaBlock>
      <MediaLink href="https://vk.com/id427319348" target="_blank"><MediaLogo src={vkLogo} /></MediaLink>
      <MediaLink href="https://www.instagram.com/gomelstroitel" target="_blank"><MediaLogo src={instLogo} /></MediaLink>
    </MediaBlock>
  </GalleryWrapper>
);

export default Gallery;
