import React from 'react';
import styled from 'styled-components';

const NavbarWrapper = styled.nav`
  background-color: navy;
  color: white;
  height: 50px;
  position: sticky;
  top: 0;
`;

const NavNavItemst = styled.ul`
  NavItemst-style: none;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin: 0;
`;

const NavItem = styled.a`
  display: block;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  :hover {
    color: red;
  }
  padding: 15px 0;
`;

const Navbar = () => {
  return (
    <NavbarWrapper>
      <NavNavItemst>
        <NavItem href="#main">Главная</NavItem>
        <NavItem href="#jobs">Виды работ</NavItem>
        <NavItem href="#advs">Наши преимущества</NavItem>
        <NavItem href="#stages">Этапы оформления заказа</NavItem>
        <NavItem href="#gallery">Наши работы</NavItem>
      </NavNavItemst>
    </NavbarWrapper>
  );
}

export default Navbar;
