import React from 'react';
import styled from 'styled-components';

import images from '../data/advantages/advantage_sprites.png';

const AdvantageWrapper = styled.div`
`;

const AdvantageImage = styled.div`
  width: 200px;
  height: 200px;
  background: url(${images}) ${prop => prop.position};
  border-radius: 3px;
  border: 1px solid grey;
  margin: 0 auto;
`;

const AdvantageTitle = styled.h3`
  text-align: center;
`;

const AdvantageText = styled.p`

`;

const Advantage = ({ advantage }) => {
  const { imagePosition, title, text } = advantage;
  return (
    <AdvantageWrapper>
      <AdvantageImage position={imagePosition} />
      <AdvantageTitle>{title}</AdvantageTitle>
      <AdvantageText>{text}</AdvantageText>
    </AdvantageWrapper>
  );
};

export default Advantage;
