import React from 'react';
import styled from 'styled-components';

import { advantages } from '../data/advantages/advantages';

import Advantage from './Advantage';

const Title = styled.h2`
  text-align: center;
`;

const Row = styled.div`
  display: flex;
`;

const Column = styled.div`
  flex: 33%;
`;

const AdvantagesList = () => {
  const advantages1 = advantages.slice(0, 3);
  const advantages2 = advantages.slice(3);
  return (
    <>
      <Title id="advs">Наши преимущества</Title>
      <Row>
        {
          advantages1.map((advantage, i) => (
            <Column key={i}>
              <Advantage advantage={advantage} />
            </Column>
          ))
        }
      </Row>
      <Row>
      {
          advantages2.map((advantage, i) => (
            <Column key={i} >
              <Advantage advantage={advantage} />
            </Column>
          ))
        }
      </Row>
    </>
  );
};

export default AdvantagesList;
